#!/usr/bin/env node
import { cac } from "cac";
import { spawn } from "child_process";
import { redBright, cyanBright, white, yellowBright } from "ansi-colors";

const CLI_NAME = "ns";
const cli = cac(CLI_NAME);

cli
  .command("")
  .allowUnknownOptions()
  .action(() => {
    console.log(cyanBright("NativeScript CLI\n"));
    console.log(
      white(
        "%s: this is a small subset of the NativeScript CLI meant for usage in %s projects.\n\nRun with %s to see usage."
      ),
      cyanBright.bold("Note"),
      cyanBright.bold("StackBlitz"),
      yellowBright("--help")
    );
  });

cli
  .command("preview", "Preview your project")
  .allowUnknownOptions()
  .action(() => {
    const previewArgs = process.argv.slice(process.argv.indexOf("preview") + 1);
    const previewCLIPath = require.resolve(
      "@nativescript/preview-cli/dist/index.js"
    );

    spawn("node", [previewCLIPath, ...previewArgs], {
      stdio: "inherit",
    });
  });

cli
  .command("clean", "Clean your project")
  .allowUnknownOptions()
  .action(() => {
    // todo: remove node_modules and lockfiles...
    console.log("Not implemented.");
  });

cli.on("command:*", () => {
  console.error(
    redBright("Unknown or unsupported command:\n\n  %s %s\n"),
    CLI_NAME,
    cli.args.join(" ")
  );
  console.log(
    white(
      "%s: this is a small subset of the NativeScript CLI meant for usage in %s projects."
    ),
    cyanBright.bold("Note"),
    cyanBright.bold("StackBlitz")
  );
  process.exit(1);
});

cli.help();
cli.parse();
